<template>
  <van-popup
    v-model="periodPopupModel"
    class="customPopup"
    :close-on-click-overlay="closeOnClickOverlay"
    :round="round"
    position="bottom"
    get-container="body"
    overlay-class="period-overlay-mask"
  >
    <van-picker
      show-toolbar
      :title="title"
      :cancel-button-text="cancelButtonText"
      :confirm-button-text="confirmButtonText"
      :value-key="valueKey"
      :columns="columns"
      :item-height="itemHeight"
      :default-index="defaultIndex"
      :visible-item-count="visibleItemCount"
      @cancel="handleClickCancel"
      @confirm="handleClickConfirm"
      @change="handleChange"
    >
      <template slot="title">
        <slot name="title">
          {{ title }}
        </slot>
      </template>
      <template #option="item">
        <!-- item -->
        <slot name="option" :data="item"></slot>
      </template>
    </van-picker>
  </van-popup>
</template>

<script>
export default {
  name: 'custpicker',
  props: {
    value: {
      type: Boolean,
      default: () => {
        return false
      }
    },
    // 
    showToolbar: {
      type: Boolean,
      default: () => {
        return false
      }
    },
    // 
    title: {
      type: [String, Number],
      default: () => {
        return ''
      }
    },
    cancelButtonText: {
      type: String,
      default: () => {
        return ''
      }
    },
    confirmButtonText: {
      type: String,
      default: () => {
        return ''
      }
    },
    // 
    valueKey: {
      type: String,
      default: () => {
        return 'text'
      }
    },
    // 
    columns: {
      type: Array,
      default: () => {
        return []
      }
    },
    // 
    itemHeight: {
      type: [Number, String],
      default: () => {
        return 44
      }
    },
    // 
    defaultIndex: {
      type: [Number, String],
      default: () => {
        return 0
      }
    },
    // 
    closeOnClickOverlay: {
      type: Boolean,
      default: () => {
        return true
      }
    },
    // 
    round: {
      type: Boolean,
      default: () => {
        return false
      }
    },
    // 
    visibleItemCount: {
      type: [Number, String],
      default: () => {
        return 5
      }
    },
    onConfirm: {
      type: Function,
      default: _ => Promise.resolve()
    },
    onCancel: {
      type: Function,
      default: _ => Promise.resolve()
    },
    onChange: {
      type: Function,
      default: _ => Promise.resolve()
    }
  },
  watch: {
    value(val) {
      this.periodPopupModel = val
    },
    periodPopupModel(val) {
      this.$emit('input', val)
    }
  },
  data() {
    return {
      periodPopupModel: false
    }
  },
  methods: {
    async handleClickConfirm(value, index) {
      let onConfirm = this.$listeners.onConfirm || this.onConfirm
      if (onConfirm && typeof onConfirm === 'function') {
        await onConfirm(value, index)
      }
      this.periodPopupModel = false
    },
    async handleClickCancel() {
      let onCancel = this.$listeners.onCancel || this.onCancel
      if (onCancel && typeof onCancel === 'function') {
        await onCancel()
      }
      this.periodPopupModel = false
    },
    async handleChange(picker, value, index) {
      let onChange = this.$listeners.onChange || this.onChange
      if (onChange && typeof onChange === 'function') {
        await onChange(picker, value, index)
      }
    }
  }
}
</script>

<style scoped lang="less">
/deep/.van-picker__toolbar {
  font-size: 16px;
}
</style>
